import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({
  columnHeaders,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
  mandatoryAttributes,
}) => {
  const { allNLSValues = [], dropdownOptions = [] } = mappedAttributes || {};

  console.log("Mapped Attributes prop:", mappedAttributes);
  console.log("columnHeaders", columnHeaders);
  console.log("static table recieved manditory Attribute", mandatoryAttributes);
  console.log("Dropdown Options :", dropdownOptions);

  const hasMatchingNLS = (header) => {
    if (!Array.isArray(allNLSValues)) {
      console.warn("allNLSValues is not an array:", allNLSValues);
      return false;
    }
    const match = allNLSValues.includes(header);
    console.log(`Header "${header}" NLS match:`, match ? "✅" : "❌");
    return match;
  };

  return (
    <Table bordered className="table-light ">
      <thead>
        <tr>
          <th className="header-styling"> Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => {
          const isMandatory = mandatoryAttributes.includes(header);
          const hasNLS = hasMatchingNLS(header);
          // Log the state of each row
          console.log(`Row ${index}:`, {
            header,
            hasNLS,
            isMandatory,
            defaultLabel: hasNLS ? header : "",
          });
          return (
            <tr key={index}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  selectedValue={selectedAttributes[index] || header}
                  onChange={handleSelectChange}
                  size="lg"
                  className="w-100"
                  disabled={!hasNLS}
                  options={{
                    defaultLabel: hasNLS
                      ? header
                      : "Please select from Drop Down", // Show header only if NLS matches
                    list: dropdownOptions.map((attr) => ({
                      // Always show all mapped attributes
                      value: attr.backendName,
                      label: attr.uiLabel,
                    })),
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
