import React, { useState, useEffect } from "react"; // Corrected import for useState
import StaticTable from "../../components/bootsrap-table/Table";
import useMassUpload from "../../hooks/Mass-Upload/useMassUpload";

const MappedList = ({ columnHeaders, mandatoryAttributes }) => {
  console.log("MappedList - Received columnHeaders:", columnHeaders);
  console.log(
    "MappedList - Received mandatoryAttributes:",
    mandatoryAttributes
  );
  const { mappedAttributes } = useMassUpload();
  const [checkedItems, setCheckedItems] = useState({});
  const [selectedAttributes, setSelectedAttributes] = useState({});
  const [filterMappedAttributes, setfilterMappedAttributes] = useState({
    allNLSValues: [],
    dropdownOptions: [],
  });

  useEffect(() => {
    if (Array.isArray(mappedAttributes)) {
      console.log("Filtered mapped attributes:", mappedAttributes);
      setfilterMappedAttributes({
        allNLSValues: mappedAttributes.map((attr) => attr.uiLabel),
        dropdownOptions: mappedAttributes,
      });
    }
  }, [mappedAttributes]);

  const handleCheckboxChange = (index) => {
    setCheckedItems({ ...checkedItems, [index]: !checkedItems[index] });
  };
  const handleSelectChange = (index, selectedValue) => {
    setSelectedAttributes({ ...selectedAttributes, [index]: selectedValue });
  };

  return (
    <StaticTable
      columnHeaders={columnHeaders}
      handleCheckboxChange={handleCheckboxChange}
      checkedItems={checkedItems}
      handleSelectChange={handleSelectChange}
      selectedAttributes={selectedAttributes}
      mappedAttributes={mappedAttributes}
      mandatoryAttributes={mandatoryAttributes}
    />
  );
};

export default MappedList;
